@import "../core/_setting.scss";
@import "../core/_css3";
@import "../core/_mixin.scss";

.hy-header {
    width: 100%;
    height: 52px;
    background-color: #f3f3f3;
    box-shadow: 0 0 1px 1px rgba(0,0,0,.04);
    overflow: hidden;
    @include transition(background .3s ease-in-out);
}
.hy-header-bg {
    background-color: #fcfcfc;
}
.hy-header-link {
    float: left;
    width: 50%;
    padding: 19px 37px 0 0;
    box-sizing: border-box;
    a {
        display: inline-block;
        height: 16px;
        overflow: hidden;
        span {
            display: block;
            line-height: 16px;
            font-size: 1.4rem;
            font-weight: bold;
            &:first-child {
                color: #16528e;
                margin-top: -16px;
                @include transition(all .3s ease-in-out);
            }
            &:last-child {
                color: #898989;
                @include transition(all .3s ease-in-out);
            }
        }
        &:hover {
            span {
                &:first-child {
                    margin-top: 0;
                }
                &:last-child {
                    margin-top: 16px;
                }
            }
        }
    }
    &:last-child {
        padding-left: 37px;
    }
    .selected {
        span {
            &:first-child {
                margin-top: 0;
                text-decoration: underline;
            }
        }
    }
}
.hy-header-intro {
    h1 {
        text-align: center;
        margin: 30px 0 25px;
    }
    .hy-intro-main {
        position: relative;
    }
    .hy-intro-page {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        @include transition(all .3s ease-in-out);
    }
    .show {
        opacity: 1;
    }
    p {
        text-align: center;
        line-height: 20px;
    }
}

.hy-main-panel {
    display: block;
    width: 100%;
    min-height: 150px;
    box-sizing: border-box;
    color: $white;
    padding-left: 20px;
    &:hover {
        color: $white;
    }
}
.purple {
    background-color: #72616e;
}
.orange {
    background-color: #e8846b;
}
.navy {
    background-color: #16528e;
}
.red {
    background-color: #e54b4b;
}
.lime {
    background-color: #a2c5bf;
}
.green {
    background-color: #167c80;
}
.blue {
    background-color: #0082c8;
}
.hy-mpanel-cont {
    display: table;
    width: 65%;
    height: 100%;
}
.hy-cont-inner {
    display: table-cell;
    vertical-align: middle;
    .hy-cont-time {
        font-size: 1.2rem;
    }
    .hy-cont-title {
        height: 48px;
        font-size: 2rem;
        line-height: 24px;
        margin-bottom: 10px;
        overflow: hidden;
    }
    .hy-cont-txt {
        height: 32px;
        font-size: 1.2rem;
        line-height: 16px;
        overflow: hidden;
    }
}
.hy-mpanel-color {
    display: table;
    width: 100px;
    height: 100%;
    position: relative;
    .hy-color-inner {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        position: relative;
    }
    .hy-color-txt {
        color: $white;
    }
    .hy-color-bg {
        display: block;
        width: 100px;
        height: 100%;
        background: rgba(0,0,0,0.23);
        position: absolute;
        top: 0;
        left: 0;
    }
}